.PartnersList{
    display: block;
}

.SwiperSlide {
    /* Add vertical paddings for slide shadow visibility */
    padding: 20px 0;
}

.PartnersItem {
    transition: transform 0.3s;
}

.PartnersItem:hover {
    transform: translateY(-10px);
}

.PartnersImg {
  border-radius: 20px;
  width: 193px;
  min-height: 193px;
  object-fit: contain;
}
